export const LiveUrl = 'https://tek-ees.com'



export const toggleVideo = (videoClass,isVideoLoaded) => {
  if(isVideoLoaded){
      let parent = document.getElementById(videoClass);
      // console.log('videoClass',videoClass)
      // console.log('parent>>>>',parent);
      // console.log('parent',parent.children[0]);
      // console.log('child',parent.children[1]);
      // console.log('parent',parent.children[2]);
      // let video = parent.children[0];
      // let playpause = parent.children[1];
      let video = parent.children[1];
      let playpause = parent.children[2];
      if (video.paused) {
        video?.play();
        playpause.setAttribute('hidden', '');
      } else {
        video?.pause();
        playpause.removeAttribute('hidden');
      }
    }else{
      // console.log('not loaded');
    }
}