import React,{ useEffect,useState } from 'react';
import axios from 'axios';
import { LiveUrl } from './../constants';
import { useParams  } from 'react-router-dom'
import VideoContainer from './../components/VideoContainer';
import ginderVideo from './../Video/gingerVideo.mp4';
import { toggleVideo } from './../constants';
import Grid from '@mui/material/Grid';
import { Markup } from 'interweave';
import {useMediaQuery} from '@mui/material';
import { useTheme } from '@mui/material/styles';
import Footer from './../components/Footer'


function OfferDetails() {
  const [offer, setOffer] = useState()
  // const [showComponent, setShowComponent] = useState(false);
  const params = useParams();
  const { id } = params;

  const theme = useTheme();
  const mediamScreen = useMediaQuery(theme => theme.breakpoints.down('md'));

  useEffect(() => {
        async function fetchProduct(){
            const { data } = await axios.get(`${LiveUrl}/${id}`)
            setOffer(data)
        }
        fetchProduct()
  },[])

  // useEffect(() => {
  //   setTimeout(() => {
  //     setShowComponent(true);
  //   }, 4000);
  // }, []);



  return (
    <div className='detailPage offerCardCon'>
      <div className='detailVCon' style={{height:'50vh'}}>
        {
          offer?.video_file ?
           <>
           {/* {showComponent &&  */}
           <VideoContainer VSource={offer?.video_file} 
                           Vscreen={'Details'} ImgS={offer?.image} VID={`detail_offer_${id}`} />
            {/* } */}
           </>
           :
           offer?.image &&  <img src={offer?.image} className="w-100 h-100" style={{objectFit:'cover'}} />
        }
      </div>
      <Grid container justifyContent={'center'} mt={5} pb={5} px={mediamScreen && 5}>
        <Grid item xs={12} md={10}>
            <Grid container py={5}  className='sectionHeading' >
                <div className='w-100' style={{display:'flex',justifyContent:'space-between'}}>
                        <h3 className="priceofferCard titleCon m-0 py-2">{offer?.title}</h3>
                        <h3 className="priceofferCard priceCon m-0 py-2"><span>{parseInt(offer?.price)}&nbsp;</span>AED</h3>
                </div>
                <Markup content={offer?.rishTextEditor} />
            </Grid>
        </Grid>
      </Grid>
      <Footer />
    </div>
  )
}

export default OfferDetails