import React,{ useEffect,useState } from 'react';
import axios from 'axios';
import { LiveUrl } from './../constants';
import Grid from '@mui/material/Grid';
import OfferCard from './OfferCard';
import {useMediaQuery} from '@mui/material';
import { useTheme } from '@mui/material/styles';
import  Footer from './Footer'

function OffersContainer() {
  const [offers, setOffers] = useState([]);
  const [tickets, setTickets] = useState([]);

  const theme = useTheme();
  const largeScreen = useMediaQuery(theme => theme.breakpoints.up('md'));
  const mediamScreen = useMediaQuery(theme => theme.breakpoints.down('md'));
  const smallScreen = useMediaQuery(theme => theme.breakpoints.down('sm'));
  

  useEffect(() => {
    async function fetchProducts(){
            let gURL = `${LiveUrl}`;
            const { data } = await axios.get(gURL);
            // console.log('data',data);
            let offersList = data.filter(offer => offer.isOffer === true)
            let ticketsList = data.filter(offer => offer.isOffer !== true)
            setOffers(offersList.sort(() => Math.random() - 0.5));
            setTickets(ticketsList.sort(() => Math.random() - 0.5));
        }
        fetchProducts()
  } , []);

  // useEffect(() => {
  //   console.log('offers here:',offers);
  // } , [offers]);


  return (
    <Grid container justifyContent={'center'} className='offerCardCon'>
        <Grid item xs={12} md={10}>
            <Grid container py={5} px={mediamScreen && 5} >
                <Grid item xs={12} >
                    {/* offers */}
                    <h3 className='sectionHeading'>Best Deals</h3>
                    <Grid container spacing={3} mb={3}>
                                {
                                    offers.map((item, index) => {
                                    return (
                                        <Grid item xs={12} sm={6} md={4} key={index} >
                                            <OfferCard  
                                                VID={`offer_${index}`}
                                                obj={item}
                                            />
                                        </Grid>
                                    )
                                    }
                                    )
                                }
                    </Grid>  
                    {/* tickets */}
                    <h3 className='sectionHeading'>Available Services</h3>
                    <Grid container spacing={3}>
                                {
                                    tickets.map((item, index) => {
                                    return (
                                        <Grid item xs={12} sm={6} md={4} key={index} >
                                            <OfferCard 
                                                VID={`ticket_${index}`}
                                                obj={item}
                                            />
                                        </Grid>
                                    )
                                    }
                                    )
                                }
                    </Grid> 
                </Grid> 
            </Grid> 
        </Grid>
        <Footer />
    </Grid>
  )
}

export default OffersContainer