import React,{ useEffect,useState } from 'react';
import logo from './../Images/logo.jpg'
import {Container,Navbar,Image} from 'react-bootstrap'
import $ from "jquery";

function Header() {
  const [scrollTop, setScrollTop] = useState(0);
  const [scrolling, setScrolling] = useState(false);

  useEffect(() => {
    const onScroll = e => {
      setScrollTop(e.target.documentElement.scrollTop);
      setScrolling(e.target.documentElement.scrollTop > scrollTop);
    };
    
    window.addEventListener("scroll", onScroll);
    // console.log(scrollTop,'scrollTop'); 
    const check = 100;
      if(scrollTop > check) {
        $('.navbar').addClass('navbar-sticky');
        $('.navbar').removeClass('bg-light');
      }else {
            $('.navbar').removeClass('navbar-sticky'); 
            $('.navbar').addClass('bg-light');
      }

    return () => window.removeEventListener("scroll", onScroll);
  }, [scrollTop]);

  return (
    <Navbar  expand="lg" className="navbar navbar-expand-lg navbar-light bg-light">
        <Container fluid>
             <Navbar.Brand className="navbar-brand" href="#home">
                <Image src={logo} alt=""  className="w-100 d-inline-block align-text-top" />
             </Navbar.Brand>
             <div className="numCon themeBtn">
                <span className="numberHeader">
                <a href="https://wa.link/ymx22s" target="_blank" className='HeaderBookingText'>Book Now</a>
                </span>
             </div>
        </Container>
    </Navbar>
  )
}

export default Header