import React,{ useEffect,useState } from 'react';
import axios from 'axios';
import { LiveUrl } from './../constants';
import { Swiper, SwiperSlide } from "swiper/react";
import mainP1 from './../Images/mainP1.jpeg'
import mainP2 from './../Images/mainP2.jpeg'

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
// import required modules
import { Autoplay,Pagination,Navigation } from "swiper";

function MainSlider() {
  const [slides, setSlides] = useState([]);

  useEffect(() => {
    async function fetchProducts(){
            let gURL = `${LiveUrl}/slides/`;
            const { data } = await axios.get(gURL);
            // console.log('data',data);
            setSlides(data.filter(slide => slide.isActive === true));
        }
        fetchProducts()
  } , []);

  useEffect(() => {
      // console.log('slides here:',slides);
    } , [slides]);

  return (
    <>
      <Swiper
        spaceBetween={30}
        speed={40000}
        centeredSlides={true}
        autoplay={{
            delay: 0,
            disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Autoplay,Pagination, Navigation]}
        className="mySwiper"
      >
        {
           slides.map((item, index) => {
            return (
              <SwiperSlide key={index}>
                  <img src={item.image} alt="mainP1" />
              </SwiperSlide>
            )
           })
        }
        
        {/* <SwiperSlide>
            <img src={mainP2} alt="mainP2" />
        </SwiperSlide> */}
      </Swiper>
    </>
  )
}

export default MainSlider