import React from 'react'
import VideoContainer from './VideoContainer';
import { Link } from 'react-router-dom'

function OfferCard({VID,obj}) {
  return (
    <div className='offerCard' >
        <div className='cardVideo' >
            { obj.video_file ?
                <VideoContainer VSource={obj.video_file} Vscreen={'Home'} ImgS={obj.image} VID={VID}   />
                :
                    obj.image &&  <img src={obj.image} className="cardImg" />
            }
        </div>
        <div className="offerCardContent">
                <h3 className="priceofferCard titleCon m-0 py-2">{obj.title}</h3>
                <h3 className="priceofferCard priceCon m-0 py-2"><span>{parseInt(obj.price)}&nbsp;</span>AED</h3>
        </div>
        {/* <div>
            <Markup content={obj.rishTextEditor} />
        </div> */}
        <Link to={`/offer/${obj.id}`} >
            <div className="themeBtn detailBtn">
                See Details
            </div>
        </Link>
    </div>
  )
}

export default OfferCard