import React,{useEffect} from 'react'
import VPlayer from 'react-vplayer';
import axios from 'axios';
import { toggleVideo } from './../constants';

function VideoContainer({VSource,VID,ImgS,Vscreen}) {
  const [isVideoLoaded, setIsVideoLoaded] = React.useState(false);
  useEffect(() => {
        document.getElementById(`vid_id_${VID}`)?.play();
  } ,[])

  const onLoadedData = () => {
    setIsVideoLoaded(true);
  };
  
  return (
    <div style={{position:'relative'}} id={`video_${VID}`} className={`videoContainer`} onClick={()=>toggleVideo(`video_${VID}`,isVideoLoaded)} >
          { Vscreen === 'Home' &&
            <img src={ImgS} className="cardImg" style={{display:isVideoLoaded?'none':'block',opacity: isVideoLoaded ? 0 : 1}} />
          }
          { Vscreen === 'Details' &&
            <img src={ImgS} className="w-100 h-100" style={{display:isVideoLoaded?'none':'block',opacity: isVideoLoaded ? 0 : 1,objectFit:'cover'}} />
          }
          <video id={`vid_id_${VID}`} className="video" style={{display:isVideoLoaded?'block':'none'}} onLoadedData={()=>onLoadedData()}  muted playsinline>
              <source src={VSource} type="video/mp4"/>
          </video>
          <div className="playpause" style={{display:isVideoLoaded?'block':'none'}} hidden></div>
    </div>
  )
}

export default VideoContainer